body {
  color: #333;
  background-color: #fdfdfd;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

h1 {
  text-align: center;
  color: #4a90e2;
  margin-bottom: 20px;
  font-size: 2em;
}

.stickyform {
  background-color: #fff;
  border-radius: 10px;
  max-width: 600px;
  margin: 5% auto;
  padding: 20px;
  box-shadow: 0 4px 8px #0000001a;
}

.stickyform form {
  flex-direction: column;
  gap: 15px;
  display: flex;
}

.stickyform input[type="text"], .stickyform select, .stickyform input[type="color"] {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 1em;
}

.stickyform input[type="color"] {
  border: none;
  width: 45px;
  height: 45px;
  padding: 0;
}

.stickyform select {
  height: 45px;
}

.stickyform button {
  color: #fff;
  cursor: pointer;
  background-color: #4a90e2;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 1.1em;
  transition: background-color .3s;
}

.stickyform button:hover {
  background-color: #357abd;
}

.kanban-board {
  justify-content: space-between;
  display: flex;
}

.kanban-column {
  background-color: #f4f4f4;
  border-radius: 5px;
  width: 30%;
  padding: 10px;
  box-shadow: 0 4px 8px #0000001a;
}

.kanban-column h2 {
  text-align: center;
}

ul {
  padding: 0;
  list-style-type: none;
}

li {
  cursor: move;
  border-radius: 5px;
  justify-content: space-between;
  margin: 5px 0;
  padding: 10px;
  display: flex;
}

li:active {
  opacity: .6;
}

.delete-btn {
  color: #f4f4f4;
  cursor: pointer;
  background-color: #fc6c38;
  border: none;
  border-radius: 5px;
  padding: 5px 15px;
}

